import React from 'react'
import InputMask from 'react-input-mask'
import { motion } from 'framer-motion'
import AnimatedText from '../AnimatedText'
import './index.scss'
import axios from 'axios'
import { toast } from 'react-toastify'

const data = [
    {
        title: '     ?',
        text: '-    /     <br/><br/> -   079     <br/><br/> -      (  3   )<br/><br/> -      COVID-19 (  3   )<br/><br/> -   ',
    },
    {
        title: '   ',
        text: '    , , , , PSP, iWatch     .<br/><br/>                   21:40  22:00    .         .           .',
    },
    {
        title: '',
        text: '            .',
    },
    {
        title: '    ?',
        text: " ,             ,    .   <a href='/go'> </a>",
    },
    {
        title: '  ?',
        text: ' ',
    },
    {
        title: '  ',
        text: '     - ,      ,            ,       .          ,        -  ,     -   .',
    },
]

const FaqList = () => {
    return (
        <div className="grid">
            {data.map((item) => (
                <FaqItem key={item.title} data={item} />
            ))}
        </div>
    )
}

const FaqItem = ({ data }) => {
    const [show, setShow] = React.useState(false)
    return (
        <div
            className={show ? 'item active' : 'item'}
            onClick={() => setShow(!show)}
        >
            <div className={show ? 'heading show' : 'heading'}>
                <h4>{data.title}</h4>
                <div className="image_wrapper">
                    <img src="./images/faq/plus.svg" alt="" />
                </div>
            </div>
            <div className="accordion">
                <h2 dangerouslySetInnerHTML={{ __html: data.text }}></h2>
            </div>
        </div>
    )
}

const Faq = () => {
    const [phone, setPhone] = React.useState('')
    const notify = () => toast.success(' !!')
    const notifyError = () => toast.error(' !!')
    const Send = () => {
        axios
            .post('https://kurs.stern.xyz:8002/api/v1/feedback/', { phone })
            .then((res) => notify())
            .catch((err) => notifyError())
    }
    return (
        <div className="faq" id="faq">
            <div className="wrapper">
                <div className="cont">
                    <div className="left">
                        <h3> </h3>
                        <p>  ?</p>
                        <InputMask
                            mask="+7 (999) 999-99-99"
                            placeholder="+7 (___) ___-__-__"
                            value={phone}
                            onChange={(e) => setPhone(e.target.value)}
                        />
                        <button onClick={() => Send()}></button>
                    </div>
                    <div className="right">
                        <FaqList />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Faq
